                <div class="seat" :class="item2 === 3 ? 'yellow' : item2 === 1 ? 'purple' : ''" v-for="(item2, index2) in item" :key="index2" v-show="item2 !== 2" @click="seatClicked(index1, index2, item2)"></div>
                <!-- <van-button :border="false" class="seat" :class="item2 === 3 ? 'yellow' : item2 === 1 ? 'purple' : ''" v-for="(item2, index2) in item" :key="index2" v-show="item2 !== 2" @click="seatClicked(index1, index2)"></van-button> -->
            </div>
            <div class="leftWrap">
