                <p>推荐座位</p>
                <div class="selectbtn" @click="recommendSeat(1)" :style="activeBtn === 1 ? 'background:#b45964' : '' ">1人</div>
                <div class="selectbtn" @click="recommendSeat(2)" :style="activeBtn === 2 ? 'background:#b45964' : '' ">2人</div>
                <div class="selectbtn" @click="recommendSeat(3)" :style="activeBtn === 3 ? 'background:#b45964' : '' ">3人</div>
                <div class="selectbtn" @click="recommendSeat(4)" :style="activeBtn === 4 ? 'background:#b45964' : '' ">4人</div>
