                }
                for(let j = 0; j < 6; j++) {
                    let k = 4
                    if (ceil + j === 7) {
                        continue
                    } 
                    if (matches[k].includes(ceil + j)) {
                        for(let i = 0; i < val; i++) {
                            this.seatList[k][ceil + j + i] = 1
                            this.selectedSeat.push([k, ceil + j + i])
                        }
                        this.seatList = this.seatList.slice(0)
                        return ''
                    } else if (matches[k].includes(floor - j)) {
                        for(let i = 0; i < val; i++) {
                            this.seatList[k][floor - j + i] = 1
                            this.selectedSeat.push([k, floor - j + i])
                        }
                        this.seatList = this.seatList.slice(0)
                        return ''
                    }
                    if (ceil + j === 10) {
                        if (matches[k].includes(7)) {
                            for(let i = 0; i < val; i++) {
                                this.seatList[k][7 + i] = 1
                                this.selectedSeat.push([k, 7 + i])
                            }
                            this.seatList = this.seatList.slice(0)
                            return ''
                        } else if (matches[k].includes(3)) {
                            for(let i = 0; i < val; i++) {
                                this.seatList[k][3 + i] = 1
                                this.selectedSeat.push([k, 3 + i])
                            }
                            this.seatList = this.seatList.slice(0)
                            return ''
                        }
                    }
                    if(matches[k].length === 0) {
                        alert(`没 ${val} 连坐了！`)
                        return ''
                    }
                }
            }
            
            // 一个人
            // if (val === 1) {
                // }
            if (val === 1) {
                const need = confirm('? 一个人还要推荐? 真的🐎')
                if (need) {
                    bestNum = 5.5
                    const floor = Math.floor(bestNum)
                    const ceil = Math.ceil(bestNum)
                    for(let j = 0; j < 6; j++) {
                        for(let k = 5; k < 9; k++) {
                            if (matches[k].includes(ceil + j)) {
                                for(let i = 0; i < val; i++) {
                                    this.seatList[k][ceil + j + i] = 1
                                    this.selectedSeat.push([k, ceil + j + i])
                                }
                                this.seatList = this.seatList.slice(0)
                                return ''
                            } else if (matches[k].includes(floor - j)) {
                                for(let i = 0; i < val; i++) {
                                    this.seatList[k][floor - j + i] = 1
                                    this.selectedSeat.push([k, floor - j + i])
                                }
                                this.seatList = this.seatList.slice(0)
                                return ''
                            }
                        }
                    }
                    for(let j = 0; j < 6; j++) {
                        for(let k = 3; k >= 0; k--) {
                            if (matches[k].includes(ceil + j)) {
                                for(let i = 0; i < val; i++) {
                                    this.seatList[k][ceil + j + i] = 1
                                    this.selectedSeat.push([k, ceil + j + i])
                                }
                                this.seatList = this.seatList.slice(0)
                                return ''
                            } else if (matches[k].includes(floor - j)) {
                                for(let i = 0; i < val; i++) {
                                    this.seatList[k][floor - j + i] = 1
                                    this.selectedSeat.push([k, floor - j + i])
                                }
                                this.seatList = this.seatList.slice(0)
                                return ''
                            }
                        }
                    }
                    for(let j = 0; j < 6; j++) {
                        let k = 4
                        if (matches[k].includes(ceil + j)) {
                            for(let i = 0; i < val; i++) {
                                this.seatList[k][ceil + j + i] = 1
                                this.selectedSeat.push([k, ceil + j + i])
                            }
                            this.seatList = this.seatList.slice(0)
                            return ''
                        } else if (matches[k].includes(floor - j)) {
                            for(let i = 0; i < val; i++) {
                                this.seatList[k][floor - j + i] = 1
                                this.selectedSeat.push([k, floor - j + i])
                            }
                            this.seatList = this.seatList.slice(0)
                            return ''
                        }
                        if(matches[k].length === 0) {
                            alert(`没座位了！`)
                            return ''
                        }
                    }

                }
            }
            

            // 等于4
            // if (matches[6].includes(bestNum)) {
            //     for(let i = 0; i < val; i++) {
            //         this.seatList[6][bestNum + i] = 1
            //         this.selectedSeat.push([6, bestNum + i])
            //     }
            //     this.seatList = this.seatList.slice(0)
            //     return ''
            // }

            // 重新赋值, 渲染页面
            this.seatList = this.seatList.slice(0)
        },
        toConfirm(val) {
            if (val !== true) {
                this.$router.push('/confirm')
            } else {
                alert('请先选座！')
            }
        },
        quit() {
            this.$router.go(-1)
        }
    },
    computed: {
        isSelected() {
            return this.selectedSeat.length === 0
        }
    }
};
</script>

